import '../js/webflow';
import '../css/normalize.css';
import '../css/webflow.css';
import '../css/modernistes.webflow.css';

import '../css/style.css';
import '../src/sliders';
import '../src/tabs';
import '../src/preloader';
import Slider from "./sliders";
import Tab from "./tabs";
import preloader from "./preloader";



document.addEventListener('DOMContentLoaded', () => {
    preloader()
    const slider1 = new Slider({
        startSlide: 2,
        interval: 6,
        sliderNav: '.slide1-nav-w',
        sliderSlide: '.slider1-content-w',
        sliderBtn: '.slider1-nav-btn',
    })

    const slider2 = new Slider({
        startSlide: 1,
        interval: 6,
        sliderNav: '.slide2-nav-w',
        sliderSlide: '.slider2-content-w',
        sliderBtn: '.slider2-nav-btn',
    })

    const slider3 = new Slider({
        sliderNav: '.slide3-nav-w',
        sliderSlide: '.slider3-content-w',
        sliderBtn: '.slider3-nav-btn',
        hoverMode: true,
    })

    const tab1 = new Tab({
        tabsClass: 'tabs-1',
        tabsMenu: 'tabs-menu',
        swiperClass:'swiper'
    })
    const tab2 = new Tab({
        tabsClass: 'tabs-1-2',
        tabsMenu: 'tabs-menu2',
        swiperClass:'swiper2',
        hover:false
    })
})
