import Swiper from 'swiper/bundle';
import 'swiper/css/bundle';

export default class Tab {
    constructor({
                    tabsClass,
                    tabsMenu,
                    swiperClass,
                    hover = true
                }
    ) {
        this.swiper = new Swiper(`.${swiperClass}`, {
            centeredSlides: false,
            allowTouchMove: false,
            spaceBetween: 20,
            slidesPerView: 1,
        })
        this.tabs = document.querySelector(`.${tabsClass}`)
        this.menuTabs = this.tabs.querySelector(`.${tabsMenu}`)
        this.contentTabs = this.tabs.querySelector(`.${swiperClass}`)
        this.slidesTabs = this.contentTabs.querySelectorAll('.swiper-slide')
        this.tabsBtn = this.menuTabs.querySelectorAll('a')
        this.tabsCards = this.contentTabs.querySelectorAll('a')
        this.hoverMode = hover
        this.checkCurrentTab = 0
        this.tabsArr = []
        this.startTab()
    }

    createTabsArr(){
        for (let i = 0; i < this.slidesTabs.length; i++) {

            this.tabsArr.push([
                i*this.slidesTabs.length,
                i*this.slidesTabs.length + this.slidesTabs.length])
        }
        // console.log(this.slidesTabs)
    }
    tabsContentEvents() {
        this.tabsCards.forEach(link=> {
            link.addEventListener("mouseenter", () => {
                this.removeHoverAll(this.tabsCards,
                    this.tabsArr[this.checkCurrentTab][0],
                    this.tabsArr[this.checkCurrentTab][1], 'hover')
                this.addHover(link, 'hover')
            }, false);
        })
    }
    removeHoverAll(nodeList, start, end, classActive) {
        for (let i = start; i < end; i++) {
            nodeList[i].classList.remove(classActive)
        }
    }

    addHover(el, classActive) {
        el.classList.add(classActive)
    }
    tabsMenuEvents() {
        this.tabsBtn.forEach((tab, index) => {
            if (tab.classList.contains('current-tab')) {
                this.checkCurrentTab = index
            }
            tab.addEventListener("click", () => {
                this.checkCurrentTab = index
                this.removeHoverAll(this.tabsBtn, 0, this.tabsBtn.length, 'current-tab')
                this.addHover(tab, 'current-tab')
                this.swiper.slideTo( index, 1500, true )
            }, false);
        })
    }
    startTab() {
        if (this.hoverMode) {
            this.tabsMenuEvents()
            this.tabsContentEvents()
            this.createTabsArr()
        } else {
            this.tabsMenuEvents()
            this.createTabsArr()
        }

    }
}
