export default class Cookie {
    constructor({
                    name,
                    value,
                    options
                }
    ) {
        this.name = name
        this.value = value
        this.options = options
    }
    getCookie() {
        let matches = document.cookie.match(new RegExp(
            "(?:^|; )" + this.name.replace(/([\.$?*|{}\(\)\[\]\\\/\+^])/g, '\\$1') + "=([^;]*)"
        ));
        return matches ? decodeURIComponent(matches[1]) : undefined;
    }

    setCookie() {
        this.options = {
            path: '/',
            ...this.options
        };

        if (this.options.expires instanceof Date) {
            this.options.expires = this.options.expires.toUTCString();
        }

        let updatedCookie = encodeURIComponent(this.name) + "=" + encodeURIComponent(this.value);

        for (let optionKey in this.options) {
            updatedCookie += "; " + optionKey;
            let optionValue = this.options[optionKey];
            if (optionValue !== true) {
                updatedCookie += "=" + optionValue;
            }
        }
        document.cookie = updatedCookie;
    }

    deleteCookie() {
        this.setCookie(this.name, "", {
            'max-age': -1
        })
    }
}
