import { gsap } from "gsap";
import Cookie from "./cookies";

export default function preloader(){
    const preloaderCookie = new Cookie({name: 'preloader', value:'true', options:{secure: true, 'max-age': 86400} })
    const preloader = document.querySelector('.preloader-w');
    const companyName = preloader.querySelector('.preloader-company-name');
    const tl = gsap.timeline({});
    if(preloaderCookie.getCookie()){
        preloader.remove()
        tl.to('.element-preloader1', {opacity: 1, duration: 2, delay:0.5, ease: "sine.out"});
        tl.to('.element-preloader2', {opacity: 0.70, duration: 2.5, ease: "sine.out"}, "<+=1");
        tl.to('.element-preloader3', {opacity: 0.70, duration: 2.5, ease: "sine.out"}, "<+=1");
    } else {
        preloaderCookie.setCookie()
        window.onload = function() {
            preloader.classList.add('hide-preloader');
            document.body.style.overflow = 'hidden';
            tl.to(companyName, {scale: 1, y: 0, duration: 2, delay:0.5, ease: "sine.out"});
            tl.to(preloader, {autoAlpha: 0, duration: 2, delay:0.2, ease: "sine.out",
                onComplete: () => document.body.style.overflow = 'visible'});
            tl.to('.element-preloader1', {opacity: 1, duration: 2, delay:0.1, ease: "sine.out"}, "<");
            tl.to('.element-preloader2', {opacity: 0.70, duration: 2.5, ease: "sine.out"}, "<+=1");
            tl.to('.element-preloader3', {opacity: 0.70, duration: 2.5, ease: "sine.out"}, "<+=1");
        }
    }
}
