export default class Slider {
    constructor({
                    startSlide = 0,
                    interval = 0,
                    sliderNav,
                    sliderSlide,
                    sliderBtn,
                    offset = 0,
                    hoverPause = false,
                    clickPagination = false,
                    hoverMode = false
                }
    ) {
        this.sliderNav = document.querySelector(sliderNav);
        this.sliderSlide = document.querySelector(sliderSlide);
        this.sliderBtn = document.querySelectorAll(sliderBtn);
        this.startSlide = startSlide;
        this.interval = interval;
        this.offset = offset
        this.hoverPause = hoverPause
        this.clickPagination = clickPagination;
        this.hoverMode = hoverMode;
        this.isPaused = false;
        this.sliderSlideLength = this.sliderSlide.children.length
        this.startSlider()
    }

    // setTransition(string = 'transition:all 1s ease-in-out;') {
    //     this.sliderSlide.querySelectorAll('*').forEach(el => {
    //         el.setAttribute('style', `${string}`)
    //     })
    // }

    #pause(pause) {
        if (this.hoverPause) {
            this.isPaused = pause
        }
    }

    #hover(boll) {
        this.sliderBtn.forEach((el, i) => {
            el.addEventListener("click", (e) => {
                if (this.clickPagination) {
                    e.preventDefault();
                    e.stopPropagation();
                    this.nextSlide(i)
                    this.#pause(true)
                }
            }, false);
            el.addEventListener("mouseenter", () => {
                this.#pause(true)
                if (boll) this.nextSlide(i)
            }, false);
            el.addEventListener("mouseleave", () => {
                this.#pause(false)
                if (boll) this.removeAll()
            }, false);
        })
    }

    timerSlider() {
        let slide = this.startSlide - 1
        let timerId = setInterval(() => {
            if (slide > this.sliderSlideLength- 2) {
                slide = 0
            } else {
                slide++
            }
            if (!this.isPaused) {
                this.nextSlide(slide)
            }
        }, this.interval * 1000);
    }

    removeAll() {
        this.removeZero(this.sliderNav)
        for (let i = 0; i < this.sliderSlide.children.length; i++) {
            this.sliderSlide.children[i].classList.remove('before-current')
            this.sliderSlide.children[i].classList.remove('current')
            this.sliderSlide.children[i].classList.remove('after-current')

        }
        for (let i = 0; i < this.sliderNav.children.length; i++) {
            this.sliderNav.children[i].classList.remove('current')

        }
    }
    removeZero(object) {
        for (let i = 0; i < object.children.length; i++) {
            if (object.children[i].classList.contains('zero-offset')){
                object.children[i].classList.remove('zero-offset')
            }
        }
    }

    addZero(object) {
        for (let i = 0; i < object.children.length; i++) {
            object.children[i].classList.add('zero-offset')
        }
    }

    #computedOff() {
        return (this.sliderNav.children.length < this.sliderSlide.children.length) ? 1 : this.offset
    }

    nextSlide(indexActive) {
        let off = this.#computedOff()

        this.removeAll()
        if (indexActive === off - 1) {
            // setTimeout(this.removeAll, 1500)
            this.removeAll()
            this.addZero(this.sliderNav)
        } else {
            this.sliderNav.children[indexActive - off].classList.add('current')
        }
        this.sliderSlide.children[indexActive].classList.add('current')

        if(indexActive < this.sliderSlideLength-1) {
            this.sliderSlide.children[indexActive + 1].classList.add('after-current')
        } else {
            this.sliderSlide.children[0].classList.add('after-current')
        }

        if(indexActive === 0) {
            this.sliderSlide.children[this.sliderSlideLength-1].classList.add('before-current')
        } else {
            this.sliderSlide.children[indexActive - 1].classList.add('before-current')
        }

    }

    startSlider() {
        // this.setTransition()
        if (this.hoverMode) {
            this.#hover(this.hoverMode)
            this.offset = 0
            this.startSlide = 0
        } else if (!this.hoverMode) {
            this.timerSlider()
            this.nextSlide(this.startSlide-1)
            this.#hover(this.hoverMode)
        }

    }
}

